export default class AppView {
  constructor(videoData, nextToken) {
    this.videoData = videoData;
    this.nextToken = nextToken;
  }

  static addSearchForm() {
    const searchBar = document.createElement('form');
    document.body.appendChild(searchBar);

    const inputText = document.createElement('input');
    inputText.name = 'search';
    inputText.type = 'text';
    inputText.placeholder = 'Search...';
    searchBar.appendChild(inputText);

    const searchBtn = document.createElement('button');
    searchBtn.classList.add('search-button');
    searchBtn.type = 'submit';
    searchBar.appendChild(searchBtn);
    const mainContainer = document.createElement('main');
    document.body.appendChild(mainContainer);
    return searchBar;
  }

  static addCards(video, slider) {
    video.map((data) => {
      const card = document.createElement('div');
      card.classList.add('card');

      const image = document.createElement('img');
      image.classList.add('video-image');
      image.src = data.snippet.thumbnails.medium.url;
      card.appendChild(image);

      const cardBody = document.createElement('div');
      cardBody.classList.add('card-body');
      card.appendChild(cardBody);

      const cardHeader = document.createElement('div');
      cardHeader.classList.add('card-header');
      cardBody.appendChild(cardHeader);


      const title = document.createElement('h3');
      cardHeader.appendChild(title);

      const videoLink = document.createElement('a');
      videoLink.classList.add('video-link');
      videoLink.href = `https://www.youtube.com/watch?v=${data.id.videoId}`;
      const linkText = data.snippet.title;
      videoLink.target = '_blank';
      videoLink.innerHTML = linkText;
      title.appendChild(videoLink);

      const channel = document.createElement('p');
      channel.innerHTML = data.videoChannelName;
      cardBody.appendChild(channel);

      const description = document.createElement('i');
      description.innerHTML = data.snippet.description;
      cardBody.appendChild(description);

      const cardBodyBottom = document.createElement('div');
      cardBodyBottom.classList.add('card-body-bottom');
      cardBody.appendChild(cardBodyBottom);

      const publishDate = document.createElement('span');
      publishDate.classList.add('publishDate');
      publishDate.innerHTML = data.snippet.publishedAt.split('T')[0].split('-').reverse().join('.');
      cardBodyBottom.appendChild(publishDate);

      const viewCount = document.createElement('span');
      viewCount.classList.add('view-count');
      viewCount.innerHTML = data.viewCount;
      cardBodyBottom.appendChild(viewCount);

      slider.appendChild(card);
      return card;
    });
  }

  static addScrollButtons() {
    const pagination = document.createElement('div');
    pagination.classList.add('paginationBar');
    const mainContainer = document.getElementsByTagName('main');
    mainContainer[0].appendChild(pagination);

    const btnPrev = document.createElement('button');
    btnPrev.classList.add('button-previous');
    pagination.appendChild(btnPrev);
    const btnPageNum = document.createElement('button');
    btnPageNum.classList.add('button-num');
    btnPageNum.innerHTML = 1;
    pagination.appendChild(btnPageNum);
    const btnNext = document.createElement('button');
    btnNext.classList.add('button-next');
    pagination.appendChild(btnNext);
  }

  showNewSearchVideo() {
    const slider = document.getElementsByClassName('slider')[0];
    slider.innerHTML = '';
    const video = this.videoData;
    AppView.addCards(video, slider);
  }

  show() {
    const video = this.videoData;
    if (document.getElementsByClassName('slider').length !== 0) {
      const existSlider = document.getElementsByClassName('slider');
      AppView.addCards(video, existSlider[0]);
    } else {
      const slider = document.createElement('div');
      slider.classList.add('slider');
      const container = document.querySelector('body > main');
      container.appendChild(slider);
      AppView.addCards(video, slider);
      AppView.addScrollButtons();
    }
  }
}
