import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.searchTerm = '';
  }

  start() {
    AppView.addSearchForm().addEventListener('submit', this.render);
  }

  static extractVideoData(data) {
    return data.items.map(clip => clip);
  }

  async render(e) {
    const prevSearchTerm = this.searchTerm;
    this.searchTerm = document.getElementsByTagName('input')[0].value;

    e.preventDefault();

    const model = new AppModel();
    const data = await model.getVideoData(this.searchTerm);
    const videoData = App.extractVideoData(data);
    const statistics = await model.getViewsCount(videoData);
    const videoStat = App.extractVideoData(statistics);
    const channels = await model.getVideoChannelName(videoData);
    const videoChannels = App.extractVideoData(channels);
    for (let i = 0; i < videoStat.length; i += 1) {
      videoData[i].viewCount = videoStat[i].statistics.viewCount;
      if (videoChannels[i]) {
        videoData[i].videoChannelName = videoChannels[i].snippet.title;
      } else {
        videoData[i].videoChannelName = 'channel name not found';
      }
    }
    const view = new AppView(videoData);
    if (!prevSearchTerm) {
      view.show();
    } else {
      view.showNewSearchVideo();
    }
    App.swipeSlide(data.nextPageToken);
  }

  static async renderNextPage(pageToken) {
    this.searchTerm = document.getElementsByTagName('input')[0].value;
    const model = new AppModel();
    const data = await model.getVideoData(this.searchTerm, pageToken);
    const videoData = App.extractVideoData(data);
    const statistics = await model.getViewsCount(videoData);
    const videoStat = App.extractVideoData(statistics);
    const channels = await model.getVideoChannelName(videoData);
    const videoChannels = App.extractVideoData(channels);

    for (let i = 0; i < videoStat.length; i += 1) {
      videoData[i].viewCount = videoStat[i].statistics.viewCount;
      if (videoChannels[i]) {
        videoData[i].videoChannelName = videoChannels[i].snippet.title;
      } else {
        videoData[i].videoChannelName = 'channel name not found';
      }
    }
    const view = new AppView(videoData);
    view.show();
    App.swipeSlide(data.nextPageToken);
  }

  static swipeSlide(token) {
    // let cardsCount;
    const btnPageNum = document.getElementsByClassName('button-num')[0];
    const btnNext = document.getElementsByClassName('button-next')[0];
    const btnPrev = document.getElementsByClassName('button-previous')[0];
    const slider = document.getElementsByClassName('slider')[0];
    const slidesLength = slider.getElementsByClassName('card').length;
    let slideCount = Number(btnPageNum.innerHTML);
    let isDown = false;
    let startX;
    let scrollLeft;
    let distance;

    async function slideScrollLeft() {
      if (slideCount < (slidesLength / 4)) {
        const sliderClip = slider.clientWidth / 10;
        let count = 0;
        const timer = setInterval(() => {
          slider.scrollLeft += sliderClip;
          count += sliderClip;
          if (count >= slider.clientWidth) {
            clearInterval(timer);
          }
        }, 50);
        slideCount += 1;
        btnPageNum.innerHTML = slideCount;
      }
      if (slideCount === (slidesLength / 4) - 1) {
        await App.renderNextPage(token);
      }
    }

    async function slideScrollRight() {
      const sliderClip = slider.clientWidth / 10;
      let count = 0;
      const timer = setInterval(() => {
        slider.scrollLeft -= sliderClip;
        count += sliderClip;
        if (count >= slider.clientWidth) {
          clearInterval(timer);
        }
      }, 50);
      if (slideCount > 1) {
        slideCount -= 1;
        btnPageNum.innerHTML = slideCount;
        await btnPageNum;
      }
    }
    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.classList.add('active');
      startX = e.pageX - slider.offsetLeft;
      // eslint-disable-next-line prefer-destructuring
      scrollLeft = slider.scrollLeft;
    });
    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.classList.remove('active');
    });
    slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      const walk = x - startX;
      distance = walk;
      slider.scrollLeft = scrollLeft - walk;
    });
    slider.addEventListener('mouseup', () => {
      isDown = false;
      slider.classList.remove('active');
      if (distance < 150) {
        const sliderClip = (slider.offsetWidth + distance) / 10;
        let count = 0;
        const timer = setInterval(() => {
          slider.scrollLeft += sliderClip;
          count += sliderClip;
          if (count >= slider.offsetWidth) {
            clearInterval(timer);
          }
        }, 50);
        if (slideCount < (slidesLength / 4)) {
          slideCount += 1;
          btnPageNum.innerHTML = slideCount;
        }
      } else if (distance > 150) {
        const sliderClip = (slider.offsetWidth - distance) / 10;
        let count = 0;
        const timer = setInterval(() => {
          slider.scrollLeft -= sliderClip;
          count += sliderClip;
          if (count >= slider.offsetWidth) {
            clearInterval(timer);
          }
        }, 50);
        if (slideCount > 1) {
          slideCount -= 1;
          btnPageNum.innerHTML = slideCount;
        }
      }
    });
    btnNext.addEventListener('click', slideScrollLeft);
    btnPrev.addEventListener('click', slideScrollRight);
    window.addEventListener('resize', this.changeElemetsOnResize);
  }
}
