export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  getVideoData(searchTerm, token) {
    const urlComponents = {
      YTurl: 'https://www.googleapis.com/youtube/v3/search?',
      API_KEY: 'key=AIzaSyBM7073i9NDnyjzIcmwadINI3vX3CbWUU0',
      param: '&type=video&part=snippet&maxResults=16&q=',
      q: searchTerm,
    };
    const result = {
      url: Object.values(urlComponents).join(''),
    };
    if (token) {
      this.state = {
        url: result.url.concat('', `&pageToken=${token}`),
      };
    } else {
      this.state = result;
    }

    return this.getClipData();
  }


  getViewsCount(videoData) {
    const urlComponentsForCount = {
      YTurl: 'https://www.googleapis.com/youtube/v3/videos?',
      API_KEY: 'key=AIzaSyBM7073i9NDnyjzIcmwadINI3vX3CbWUU0&id=',
      id: videoData.map(d => d.id.videoId).join(','),
      param: '&part=snippet,statistics',
    };
    const resultForCount = {
      url: Object.values(urlComponentsForCount).join(''),
    };
    this.state = resultForCount;
    return this.getClipData();
  }

  getVideoChannelName(videoData) {
    const urlComponentsForChannelName = {
      YTurl: 'https://www.googleapis.com/youtube/v3/channels?',
      API_KEY: 'key=AIzaSyBM7073i9NDnyjzIcmwadINI3vX3CbWUU0&id=',
      id: videoData.map(d => d.snippet.channelId).join(','),
      param: '&part=snippet',
    };
    const resultForChannelName = {
      url: Object.values(urlComponentsForChannelName).join(''),
    };
    this.state = resultForChannelName;
    return this.getClipData();
  }

  async getClipData() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return data;
  }
}
